<template>
  <v-app id="inspire" class="papel">
    <navbar />

    <SideBar />

    <v-divider color="white" class="mt-1" dark></v-divider>
    <v-container>
      <v-row>
        <v-col cols="12" sm="12">
          <v-toolbar flat color="rgba(0,0,0,0)" class="mt-n5">
            <v-spacer></v-spacer>
          </v-toolbar>

          <v-row class="px-5 mt-n6 ml-5">
            <v-col cols="12" sm="3" v-for="list in lists" :key="list.id">
              <v-card
                align="center"
                class="rounded-circle border pt-10"
                width="200"
                height="200"
                flat
              >
                <v-icon size="60">
                  {{ list.icon }}
                </v-icon>

                <v-card-text class="grey--text text-lg-h6">
                  {{ list.title }}
                </v-card-text>

                <v-btn
                  absolute
                  color="#243e57"
                  class="white--text"
                  fab
                  left
                  top
                >
                  <h2>{{ list.count }}</h2>
                </v-btn>
              </v-card>
            </v-col>
          </v-row>
         
        </v-col>
         <div style="display: flex; margin-left: -82px; margin-top: 1%">
            <v-col cols="12" sm="7">
              <GChart
                type="LineChart"
                :data="chartData1"
                :options="chartOptions1"
              />
            </v-col>
            <br />
            <br />
            <v-col cols="12" sm="5">
              <v-card height="" flat color="transparent" class="mt-n5">
               

                <div class="pizza">
                  <GChart
                    :settings="{ packages: ['corechart'], callback: drawChart }"
                    type="PieChart"
                     @ready="drawChart"
                    :data="chartData"
                    :options="chartOptions"
                  />
                </div>
              </v-card>
            </v-col>
          </div>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
import { GChart } from "vue-google-charts/legacy";

import Navbar from "../components/Navbar.vue";
import SideBar from "../components/SideBar.vue";
import { api } from "@/conf/api";

export default {
  name: "DashboardPrincipal",
  async beforeMount() {
   
  this.drawChart()
    
    //const { setIntervalAsync } = require("set-interval-async/legacy");
    
    setInterval(() => this.drawChart(), 20000);
    setInterval(() => this.graficoFluxo(), 2000);
    setInterval(() => this.realtime(), 2000);
    setInterval(() => this.reload(), 15000);

  },

  data: () => ({
    chartData1: [],
   
    chartOptions1: {
      title: "Fluxo de ligações por hora",
      curveType: "function",
      legend: { position: "bottom" },
      width: 800,
      height: 350,
    },
    chartData: [["Ligações", "Quantidade"],["Atendidas", 0],["Abandonadas",0]],
    chartOptions: {
      title: "Classificação de chamadas",
      width: 600,
      height: 350,
      
    },
    oi: null,
    lists: [
      {
        id: 1,
        icon: "fa fa-user-plus",
        title: "Operadores Logados",
        count: 0,
      },
      {
        id: 2,
        icon: "fa fa-user-times",
        color: "red",
        title: "Operadores Deslogados",
        count: 0,
      },
      {
        id: 3,
        icon: "fa fa-pause-circle",
        title: "Operadores em pausa",
        count: 0,
      },
      {
        id: 4,
        icon: "fa fa-phone-square",
        title: "Em ligação",
        count: 0,
      },
    ],
    ligacao: [],
    dados: [],
    compara: [],
    lixo: [],
    logado: [],
    
  }),

  components: {
    SideBar,

    GChart,
    //Table
    Navbar,
  },

  methods: {
    currentDateTime() {
      const current = new Date();
      const date =
        current.getFullYear() +
        "-" +
        (current.getMonth() + 1) +
        "-" +
        current.getDate();
      //const time = "23:59:59";
      const dateTime = date;

      return dateTime;
    },
    PastDateTime() {
      const current = new Date();
      const date =
        current.getFullYear() +
        "-" +
        (current.getMonth() + 1) +
        "-" +
        current.getDate();
      //const time = "00:00:00";
      const dateTimeB = date ;

      return dateTimeB;
    },
    drawChart: async function () {
      let a = await api.get(
        `dados/dashpizza/${this.PastDateTime()}/${this.currentDateTime()}`
      );
      ////console.log(a.data.dados);
      if (!a.data.dados) {
      //  //return alert("não veio nada");
      }
      if (a.data.dados.length == 0) {
      //  //return alert("Não existe ligação");
      }
      let join = a.data.dados.length;

      let b = await api.get(
        `dados/filaabandono/${this.PastDateTime()}/${this.currentDateTime()}`
      );
      ////console.log(`${this.PastDateTime()}/${this.currentDateTime()}`)
      if (!b.data.dados) {
        //return alert("não veio nada2");
      }
      if (b.data.dados.length == 0) {
        //return alert("Não existe ligação2");
      }
      
      let abandon = b.data.dados.length;
      ////console.log(abandon, join);

      let atendida = join - abandon;
      this.chartData[1][1] = 0
      
      this.chartData[2][1] =  0
      ////console.log(atendida);
      /*
      this.chartData.push(["Ligações", "Quantidade"]);
      this.chartData.push(["Atendidas", atendida]);
      this.chartData.push(["Abandonadas", abandon]);
*/    
      this.chartData[1][1] = atendida
      
      this.chartData[2][1] = abandon

      //console.log("atendida:",atendida, "abandonada",abandon)
      console.log(this.chartData)
    },
    realtime: async function () {
      let liga = await api.get("dados/dashligacao");

      //console.log(liga.data.dados);
      this.lists[3].count = liga.data.dados.length;
      ////console.log(realliga);
      //this.ligacao.push([realliga]);

      let loga = await api.get("dados/dashlogados");

      //console.log(loga.data.dados);
      this.lists[0].count = loga.data.dados.length;
   //   //console.log(realloga);
     // this.logado.push([realloga]);

      let pausa = await api.get("dados/dashpausados");

      //console.log(pausa.data.dados);
      this.lists[2].count = pausa.data.dados.length;
      ////console.log(realpausa);
      //this.logado.push([realpausa]);
      let deslogado = await api.get("dados/dashdeslogados");

      //console.log(pausa.data.dados);
     // this.lists[1].count = deslogado.data.dados.length;
      let todosoperadores = deslogado.data.dados.length;
      //console.log(todosoperadores);

      let logados = await api.get("dados/dashlogados");
      let logadosonline = logados.data.dados.length;

      let deslogadosoperadores;
      deslogadosoperadores = todosoperadores - logadosonline;
      //console.log("Teste anna: ", loga);
   //console.log("Todos operadores online",loga);
     //onsole.log("Todos operadores deslogados",deslogadosoperadores);
      //this.logado.push([realpausa]);
       this.lists[1].count = deslogadosoperadores;
    },
     reload: function() {
      setInterval(document.location.reload(true), 5000);
    },
    graficoFluxo: async function(){
//console.log(atendida)
//console.log(this.chartData)
      let fluxo = await api.get(`dados/fluxohora/${this.PastDateTime()}`);
      ////console.log(fluxo.data.dados);
      if (!fluxo.data.dados) {
     //   return alert("não veio nada");
      }
      if (fluxo.data.dados.length == 0) {
       // return alert("Não existe ligação");
      }
      let completo = fluxo.data.dados;
      //////console.log("teste",completo);
      this.chartData1 = [] 
      this.chartData1.push(["Horas", "Chamadas"]);
      completo.forEach((d) => {
        let hora = d.hora;
        let total = d.total;
        ////console.log(hora, total);
    //console.log(hora, total)
        this.chartData1.push([hora, total]);
    //  this.chartData1[1][0] = hora;
    //  this.chartData1[1][1] = total;
      });
      
    },
    
    
    },
   
    teste() {
      this.lists.count = "4";
    },
    watch: {
      data: function(){
        this.chart.destroy()
        this.chart.render()
      }
    }
  };

</script>
<style scoped>
.fa-user-plus {
  color: green;
}
.fa-user-times {
  color: red;
}
.fa-pause-circle {
  color: orange;
}
.fa-phone-square {
  color: steelblue;
}

.border {
  border: 2px solid #243e57 !important;
}
.v-btn--fab.v-size--default.v-btn--absolute.v-btn--top {
  top: 65px !important;
}
.v-btn--absolute.v-btn--left,
.v-btn--fixed.v-btn--left {
  left: -26px !important;
}
</style>